* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.container {
  margin: 0 auto;
  max-width: 1440px;
}
/* bellota-text-regular - latin_cyrillic */
@font-face {
  font-family: "Bellota Text";
  font-style: normal;
  font-weight: 400;
  src: url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.eot"); /* IE9 Compat Modes */
  src: local(""),
    url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.eot?#iefix")
      format("embedded-opentype"),
    /* IE6-IE8 */
      url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.woff2")
      format("woff2"),
    /* Super Modern Browsers */
      url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.woff")
      format("woff"),
    /* Modern Browsers */
      url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.ttf")
      format("truetype"),
    /* Safari, Android, iOS */
      url("./assets/fonts/bellota-text-v16-latin_cyrillic-regular.svg#BellotaText")
      format("svg"); /* Legacy iOS */
}
