.main-section {
  background: url("../../assets//images/main_background3.png") top;
  background-size: cover;
  background-repeat: no-repeat;
  height: 100%;
}

.main-section-wrapper {
  position: relative;
  height: 80vh;
}

.main-section-title {
  font-size: 96px;
  font-weight: 700;
  font-family: "Montserrat", sans-serif;
  color: #fff;
  text-align: center;
  padding-top: 24px;
  text-transform: uppercase;
}

.main-section-text {
  text-transform: uppercase;
  width: 100%;
  position: absolute;
  bottom: 24px;
  font-size: 28px;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  text-align: center;
  padding: 0 104px;
  left: 50%;
  transform: translate(-50%, 0%);
}
