.ama-section {
  background-color: #222;
  padding: 72px;
}

.ama-section-content {
  display: flex;
  align-items: center;
}

.ama-title {
  font-size: 24px;
  color: #7d7d71;
  font-family: "Montserrat", sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  text-align: center;
  padding-bottom: 36px;
}

.ama-text-block {
  font-size: 24px;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  text-align: justify;
  padding: 16px;
  line-height: 42px;
}

.ama-logo-wrapper {
  width: 30%;
  height: 100%;
  background-color: #7d7d71;
}

.ama-logo {
  width: 320px;
  float: left;
  shape-outside: circle(50%);
  margin-right: 96px;
}

.ama-main-text {
  width: 70%;
}

.ama-ul-text {
  width: 30%;
  margin: 48px;
  font-size: 24px;
}

li::marker {
  color: #7d7d71;
  font-size: 32px;
}

ul {
  color: #7d7d71;
  text-transform: uppercase;
}

li {
  color: #fff;
  font-weight: 400;
  text-transform: none;
  padding: 8px 0;
  line-height: 32px;
}

.fingers-section {
  display: flex;
  max-height: 500px;
  background-color: #2b2b2b;
  align-items: center;
  position: relative;
}

.ama-fingers-block {
  background: url("../../assets/images/ama_fingers.jpg") center no-repeat;
  background-size: cover;
  width: 60%;
  min-height: 500px;
}

.ama-fingers-text {
  font-size: 32px;
  color: #fff;
  font-family: "Montserrat", sans-serif;
  text-align: justify;
  padding: 24px;
  line-height: 42px;
  width: 40%;
  z-index: 13;
}
